@charset "UTF-8";
.search-form {
  display: flex;
  width: 290px;
  flex-wrap: wrap; }
  .search-form .search-input {
    display: inline-block;
    width: calc(100% - 55px);
    height: 36px;
    font-size: 14px;
    padding-left: 10px;
    border: 1px solid #eff8fd;
    background-color: #eff8fd;
    border-radius: 0;
    color: #1c1c1c;
    margin: 0; }
  .search-form > button {
    display: inline-block;
    width: 40px;
    height: 40px;
    vertical-align: top;
    border: 1px solid #eff8fd;
    border-radius: 0;
    background-color: #7bbfec;
    background-image: url(/images/control/icon_search.svg);
    background-size: 21px;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer; }
  .search-form .sign-search {
    font-family: PTSans;
    color: #ffffff;
    font-size: 12px;
    font-weight: 400;
    padding-top: 5px; }

header {
  background-color: #80b9e6; }

.wrapper-header {
  display: flex;
  width: 1060px;
  flex-wrap: wrap;
  margin: 0 auto; }
  .wrapper-header .header-body {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    padding: 37px 0; }
    .wrapper-header .header-body .title {
      margin: 0; }
      .wrapper-header .header-body .title .logo-link {
        display: block;
        font-family: PTSans;
        color: #ffffff;
        font-size: 32px;
        font-weight: 400;
        font-weight: 700; }
        .wrapper-header .header-body .title .logo-link .signature {
          font-family: PTSans;
          color: #ffffff;
          font-size: 16px;
          font-weight: 400; }
        .wrapper-header .header-body .title .logo-link p {
          margin: 0; }
    .wrapper-header .header-body .menu-header {
      display: flex; }
      .wrapper-header .header-body .menu-header ul {
        list-style: none;
        padding: 0 20px;
        margin: 0; }
        .wrapper-header .header-body .menu-header ul a {
          display: block;
          font-family: PTSans;
          color: #ffffff;
          font-size: 14px;
          font-weight: 400;
          padding-bottom: 5px;
          transition: 1s;
          position: relative; }
          .wrapper-header .header-body .menu-header ul a:after {
            content: '';
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            height: 1px;
            background: #ffffff;
            opacity: 0;
            transition: opacity .3s,transform .3s;
            transform: translateY(10px); }
          .wrapper-header .header-body .menu-header ul a:hover:after {
            opacity: 1;
            transform: translateY(-4px); }

.main-menu {
  width: 100%;
  background-color: #338ec7; }
  .main-menu ul {
    display: flex;
    width: 1060px;
    list-style: none;
    padding: 0;
    margin: 0 auto; }
    .main-menu ul li:after {
      content: '•';
      display: inline-block;
      vertical-align: middle;
      font-family: PTSans;
      color: #ffffff;
      font-size: 12px;
      font-weight: 400; }
    .main-menu ul a {
      display: inline-block;
      font-family: PTSans;
      color: #ffffff;
      font-size: 16px;
      font-weight: 400;
      padding: 18px 10px;
      transition: .5s; }
      .main-menu ul a:hover {
        background-color: #7bbfec;
        color: #3890c4; }

html,
body {
  margin: 0;
  padding: 0; }
  html a,
  body a {
    text-decoration: none; }
